import { useState } from 'react';
import { useRouter } from 'next/router';
import api from '../utils/api';
import Navbar from '../components/Navbar';
import Link from 'next/link';

export default function Login() {
const [email, setEmail] = useState('');
const [password, setPassword] = useState('');
const router = useRouter();

const submit = async () => {
try {
const res = await api.post('/auth/login', { email, password });
localStorage.setItem('token', res.data.token);
router.push('/dashboard');
} catch (err) {
alert(err.response?.data?.message || 'Login failed');
}
};

return (
<> <Navbar /> <div className="container mx-auto max-w-md mt-12"> <div className="card"> <h2 className="text-2xl font-bold mb-4">Login</h2>
<input
className="w-full border p-2 rounded mb-3"
placeholder="Email"
value={email}
onChange={e => setEmail(e.target.value)}
/>
<input
className="w-full border p-2 rounded mb-3"
placeholder="Password"
type="password"
value={password}
onChange={e => setPassword(e.target.value)}
/> <div className="flex justify-between items-center"> <button onClick={submit} className="btn btn-primary">Login</button> <Link href="/register" className="text-sm text-gray-500">No account? Register</Link> </div> </div> </div>
</>
);
}
